import React from "react";
import logo from "../img/logo_kolor.jpg";
import "../css/SettingsPage.css";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
// import ToggleButton from "react-toggle-button";
import { connect } from "react-redux";
import { ustawMinuty } from "../actions/settingsActions";
import { SettingsState } from "../reducers/settingsReducer";
import { compose } from "recompose";
import { Dropdown } from 'semantic-ui-react'

interface ComponentProps {}

interface LocalProps {}

interface StateProps {
  minuty: number;
}

interface DispatchProps {
  ustawMinuty: typeof ustawMinuty;
}

type Props = ComponentProps & LocalProps & StateProps & DispatchProps;

export class SettingsPage extends React.Component<Props, LocalProps> {
  public constructor(props: Props) {
    super(props);
    this.state = {};
  }

  

  public ustawMinuty = (e: any) => {
    const { ustawMinuty } = this.props;
    if (ustawMinuty) {
      ustawMinuty(Number(e.currentTarget.value));
    }
  };

  render() {
    const { minuty } = this.props;
    const friendOptions = [
      {
        key: 'Marcin W',
        text: 'Marcin W',
        value: 'Marcin W',
        image: { avatar: true, src: 'https://react.semantic-ui.com/images/avatar/small/jenny.jpg' },
      },
      {
        key: 'Arek',
        text: 'Arek',
        value: 'Arek',
        image: { avatar: true, src: 'https://react.semantic-ui.com/images/avatar/small/elliot.jpg' },
      },
      {
        key: 'Bronek',
        text: 'Bronek',
        value: 'Bronek',
        image: { avatar: true, src: 'https://react.semantic-ui.com/images/avatar/small/stevie.jpg' },
      },
      {
        key: 'Marcin J',
        text: 'Marcin J',
        value: 'Marcin J',
        image: { avatar: true, src: 'https://react.semantic-ui.com/images/avatar/small/christian.jpg' },
      }
    ]

    const roundOptions = [
      {
        key: '1',
        text: '1',
        value: '1'  
      },
      {
        key: '2',
        text: '2',
        value: '2'  
      },
      {
        key: '3',
        text: '3',
        value: '3'  
      },
      {
        key: '4',
        text: '4',
        value: '4'  
      },
      {
        key: '5',
        text: '5',
        value: '5'  
      }
    ]

    return (
      <div className="SettingsPage">
        <Container>
          <Row>
            <Col className="ColumnBlack">
              <img src={logo} alt="logo" />
            </Col>
          </Row>
          <Row>Rundy:</Row>
          <Row>
            <Dropdown
              placeholder="Wybierz ile rund"
              fluid
              selection
              options={roundOptions}
            />
          </Row>
          <Row>
            <Col>Czas w minutach:</Col>
            <input
              type="number"
              className="SettingsInput"
              defaultValue={minuty.toString()}
              onChange={this.ustawMinuty}
            ></input>
          </Row>
          <Row>
            <Col>Przerwa w sekundach:</Col>
            <input type="number" className="SettingsInput"></input>
          </Row>
          <Row>
            <Col>Sygnał co minutę:</Col>
            {/* <ToggleButton
              inactiveLabel={"Nie"}
              activeLabel={"Tak"}
              value={this.state.sygnalCoMin}
              onToggle={value => {
                this.setState({
                  sygnalCoMin: !value
                });
              }}
            /> */}
          </Row>
          <Row>SoundPack:</Row>
          <Row>
            <Dropdown
              placeholder="Wybierz trenera"
              fluid
              selection
              options={friendOptions}
            />
          </Row>
        </Container>
      </div>
    );
  }
}

const container = compose<Props, ComponentProps>(
  connect<StateProps, DispatchProps, {}, SettingsState>(
    (_state: SettingsState, _props: any) => {
      return {
        minuty: _state.minuty
      };
    },
    {
      ustawMinuty
    }
  )
)(SettingsPage);

export default container;
